<template>
    <div>

        <!--首页列表模块-->
        <div v-for="item in list" :key="item.id">

            <!--标题-->
            <div class="index_title">
                <h2 class="normal">
                    <router-link :to="targetRouterArticle(item.id)">
                        {{ item.title }}
                    </router-link>
                </h2>
            </div>
            <!--标题 结束-->

            <!--简介-->
            <div class="index_intro">
                <p v-html="item.intro"></p>
            </div>
            <!--简介 结束-->

            <!--标签-->
            <div>
                <div class="index_tag">
                    <router-link :to="targetRouterArchive(item.archive)">
                        <i class="icontags-fill"></i> {{ item.archive }}
                    </router-link>
                </div>
                <div class="index_tag index_right_tag">
                    <p>
                        <i class="iconField-time"></i> {{ item.time }}
                    </p>
                </div>
            </div>
            <!--标签 结束-->

            <!--分割线-->
            <p class="index_split"></p>
            <!--分割线 结束-->

        </div>
        <!--首页列表模块 结束-->

        <!--加载更多文章-->
        <div class="index_more">
            <a href="javascript:void(0);" @click.preventDefault="fetchMoreData()">
                <div class="index_more_btn">
                    加载更多 <i class="iconcaret-down"></i>
                </div>
            </a>
        </div>
        <!--加载更多文章 结束-->

    </div>
</template>

<script>
import { getArticleList, getMoreArticleList } from '../api/index.js'

/* eslint-disable */
export default {
    data() {
        return {
            list: [],
            count: 21
        }
    },
    methods: {
        fetchData() {
            getArticleList().then(response => {
                this.list = response.data
            })
        },
        fetchMoreData() {
            var data={
                'count': this.count
            }
            getMoreArticleList(data).then(response => {
                response.data.forEach( item => {
                    this.list.push(item)
                } )
            })
            this.count += 20
        },
        targetRouterArticle(article_id) {
            return '/article/' + article_id
        },
        targetRouterArchive(archive) {
            return '/archive/' + archive
        }
    },
    created() {
        this.fetchData()
    }
}
</script>

