import request from '@/utils/request'

export function getArticleList() {
  return request({
    url: '/getarticlelist',
    method: 'get',
    headers: { 'content-type': 'application/x-www-form-urlencoded' }
  })
}

export function getMoreArticleList(data) {
  return request({
    url: '/getarticlelist',
    method: 'post',
    headers: { 'content-type': 'application/x-www-form-urlencoded' },
    data: data
  })
}
