<template>
    <div id="app">

        <!-- 侧边栏 -->
        <div role="navigation" id="foo" class="nav-collapse">
            <ul>
                <div class="logo">
                    <img src="./assets/logo.png" alt="logo" class="logoimg">
                    <h3 class="author">Kuari</h3>
                </div>
                <li :class="{ active: menu_active.index }"><router-link to="/">首页</router-link></li>
                <li :class="{ active: menu_active.archive }"><router-link to="/archive">分类</router-link></li>
                <!--<li :class="{ active: menu_active.excerpt }"><router-link to="/excerpt">摘抄</router-link></li>-->
                <li :class="{ active: menu_active.aboutme }"><router-link to="/aboutme">关于我</router-link></li>
            </ul>
        </div>
        <!-- 侧边栏 结束 -->

        <!-- 主要内容 -->
        <div role="main" class="main">
            <a href="javascript:void(0);" class="nav-toggle" @click="menutoggle()">Menu</a>

            <!-- 插入内容 -->
            <transition name="fade-transform">
                <router-view></router-view>
            </transition>
            <!-- 插入内容 结束 -->

        </div>
        <!-- 主要内容 结束 -->

    </div>
</template>

<script>
export default {
    name: 'app',
    data() {
        return {
            menu_active: {
                'index': true,
                'archive': false,
                'excerpt': false,
                'aboutme': false
            }
        }
    },
    components: {
    },
    methods: {
        menutoggle() {
            // 移动端菜单的开合
            var ele = document.getElementById('foo')
            if (ele.style.display == 'block') {
                ele.style.display = 'none'
            } else {
                ele.style.display = 'block'
            }
        },
        menu_active_toggle(node) {
            // 其他选项取消active
            this.menu_active.index = false
            this.menu_active.archive = false
            this.menu_active.excerpt = false
            this.menu_active.aboutme = false
            // 传入新的路由的选项高亮
            this.menu_active[node] = true
        },
        refreshMenu() {
            // 当直接刷新过来时需要调整菜单激活状态
            var url = window.location.href
            var urlmap = url.split('#')
            if ( urlmap[1].length != 1 )
                this.menu_active_toggle(urlmap[1].slice(1))
        }
    },
    created() {
        this.refreshMenu()
    },
    watch: {
        '$route' (to, from) {
            // 监听路由变化
            this.menu_active_toggle( to.name )
        }
    }
}
</script>

<style>
.logo {
    margin-bottom: 60px;
    text-align: center;
}
.logoimg {
    width: 65%;
    height: 65%;
    min-width: 140px;
    min-height: 140px;
}
.author {
    color: white;
}
/* fade-transform */
.fade-transform-leave-active {
    transition: all .8s;
    display: none;
}
.fade-transform-enter-active {
    transition: all .8s;
}

.fade-transform-enter {
    opacity: 0;
    transform: translateX(-30px);
}
.fade-transform-leave-to {
    opacity: 0;
    transform: translateX(30px);
}
</style>
