<template>
    <div>

        <!--标题-->
        <div>
            <h2 class="article_title">
                {{ title }}
            </h2>
        </div>
        <!--标题 结束-->

        <!--标签-->
        <div class="article_tag_node">
            <div class="article_tag">
                <router-link :to="targetRouterArchive(archive)">
                    <i class="icontags-fill"></i> {{ archive }}
                </router-link>
            </div>
            <div class="article_tag article_right_tag">
                <p>
                    <i class="iconField-time"></i> {{ time }}
                </p>
            </div>
        </div>
        <!--标签 结束-->

        <!--分割线-->
        <p class="article_split"></p>
        <!--分割线 结束-->

        <!--正文-->
        <div class="article_content clear">
            <span v-html="content" v-hljs></span>
        </div>
        <!--正文 结束-->

    </div>
</template>

<script>
import { getArticle } from '../api/article.js'

export default {
    data() {
        return {
            article_id: this.$route.params.article_id,
            title: '',
            content: '',
            time: '',
            archive: ''
        }
    },
    methods: {
        fetchData() {
            getArticle(this.article_id).then( response => {
                this.title = response.data.title
                document.title = response.data.title + ' - 还是少年中'
                this.content = response.data.content
                this.time = response.data.time
                this.archive = response.data.archive
            } )
        },
        targetRouterArchive(archive) {
            return '/archive/' + archive
        }
    },
    created() {
        this.fetchData()
    },
}
</script>

