    text-decoration: underline;
}
.article_content a:visited {
    color: #600090;
}
.article_title {
    margin-top: 0px;
    margin-bottom: 15px;
}
.article_tag_node {
    margin-top: 15px;
}
.article_tag {
    float: left;
}
.article_right_tag {
    margin-left: 20px;
}
.clear {
    clear: both;
}
