<template>
    <div>

        <!--首页列表模块-->
        <div v-for="item in list" :key="item.id">

            <!--标题-->
            <div class="index_title">
                <h2 class="normal">
                    <router-link :to="targetRouterArticle(item.id)">
                        {{ item.title }}
                    </router-link>
                </h2>
            </div>
            <!--标题 结束-->

            <!--简介-->
            <div class="index_intro">
                <p v-html="item.intro"></p>
            </div>
            <!--简介 结束-->

            <!--标签-->
            <div>
                <div class="index_tag">
                    <router-link :to="targetRouterArchive(item.archive)">
                        <i class="icontags-fill"></i> {{ item.archive }}
                    </router-link>
                </div>
                <div class="index_tag index_right_tag">
                    <p>
                        <i class="iconField-time"></i> {{ item.time }}
                    </p>
                </div>
            </div>
            <!--标签 结束-->

            <!--分割线-->
            <p class="index_split"></p>
            <!--分割线 结束-->

        </div>
        <!--首页列表模块 结束-->

    </div>
</template>

<script>
import { getArchive } from '../api/archive.js'

/* eslint-disable */
export default {
    data() {
        return {
            list: [],
        }
    },
    methods: {
        fetchData() {
            var archive = this.$route.params.archive
            getArchive(archive).then(response => {
                this.list = response.data
            })
        },
        targetRouterArticle(article_id) {
            return '/article/' + article_id
        },
        targetRouterArchive(archive) {
            return '/archive/' + archive
        }
    },
    created() {
        this.fetchData()
    }
}
</script>

<style>
.index_title a:hover {
    text-decoration: underline;
}
.index_intro {
    color: #999;
    margin-top: 5px;
    margin-bottom: 5px;
}
.index_split {
    height: 1px;
    background-color: #999;
    margin-top: 5px;
    margin-bottom: 10px;
}
.index_tag {
    float: left;
}
.index_right_tag {
    margin-left: 20px;
}
h2.normal {
    font-weight: normal
}
.index_more {
    text-align: center;
    margin-top: 30px;
    clear: both;
}
</style>
